/* Global styles for the Admin Panel */
.body {
    background-color: white;
    color: black;
    font-family: Arial, sans-serif;
    padding: 20px;
  }
  
  h1, h2 {
    color: black;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  th, td {
    border: 1px solid #ccc;
    padding: 10px;
    text-align: left;
  }
  
  tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  tr:nth-child(odd) {
    background-color: #f1f1f1;
  }
  
  input[type="number"] {
    background-color: white;
    color: black;
    border: 1px solid #ccc;
    padding: 5px;
    width: 60px;
  }
  
  .button {
    background-color: black;
    color: white;
    border: 1px solid white;
    padding: 10px 20px;
    cursor: pointer;
    margin-top: 10px;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: #444;
  }
  .notification {
    color: #ffffff; /* White text */
    padding: 10px 20px;
    background-color: #86e495; /* Dark gray background */
    border-radius: 8px;
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
    font-weight: bold;
    transition: opacity 0.5s ease-in-out;
    opacity: 0;
    animation: fadeInOut 3s forwards;
  }
  
  /* Fade-in and fade-out animation */
  @keyframes fadeInOut {
    0% {
      opacity: 0;
    }
    10% {
      opacity: 0.7;
    }
    90% {
      opacity: 0.7;
    }
    100% {
      opacity: 0;
    }
  }